/*grid*/
$grid-breakpoints: (
  xs:0,
  mb:376px,
  sm: 576px,
  md: 768px,
  lg: 991px,
  xl: 1280px,
  2xl: 1440px
);
$container-max-widths: (
  mb:100%,
  sm:100%,
  md: 720px,
  lg: 960px,
  xl: 1140px,
);

/*color*/
$primary :#772400;
$secondary :#D54100;
$light:#F0AD25;
$dark:#FAFAFA;
$theme-colors: () !default;
$theme-colors: map-merge(
  (
    'extra':#887676,
    'gray':#6C757D,
    "black":  #212529,
    'lighter':#CED4DA
  ),
  $theme-colors
);

$body-color:$primary;
$link-color: $primary;
$link-hover-color:$secondary;
$link-hover-decoration: none;


/*  font-size */
$font-size-base: 1rem;
$font-size: () !default;
$font-size: map-merge(
  (
    'sm': ($font-size-base *0.875),
    'base': $font-size-base,
    'x1': ($font-size-base * 1.25),
    'x2' :($font-size-base * 1.5),
    'x3': ($font-size-base * 1.75),
    'x4': ($font-size-base * 2),
    'x5': ($font-size-base * 2.25),
    'x6': ($font-size-base * 2.5),
    'x7': ($font-size-base * 3),
    'x8': ($font-size-base * 5),
  ),
  $font-size
);

/*font */

$font-family-sans-serif:'Noto Sans TC', sans-serif;

/*spacer */
$spacer: 1rem ;
$spacers: (
  
  0:0,
  1:($spacer * 0.25),
  2: ($spacer * 0.5),
  3:  ($spacer * 0.75) ,
  4:$spacer,
  5: ($spacer * 1.25),
  6: ($spacer * 1.5),
  7: ($spacer * 1.75),
  8: ($spacer * 2),
  9: ($spacer * 2.25),
  10: ($spacer * 2.5),
  11: ($spacer * 3),
  14: ($spacer * 3.5),
  15: ($spacer * 3.75),
  16: ($spacer * 4),
  17: ($spacer * 4.5),
  18: ($spacer * 5),
  19: ($spacer * 6.25),
  20: ($spacer * 6.75),
  21: ($spacer * 8.25),
  22: ($spacer * 11.75),
  23: ($spacer * 13.5),
);


/* form */

$input-height:auto;
$input-height-lg:auto;